import React from "react";
import { Checkbox, FormControlLabel } from "@mui/material";

export const InputCheckbox = ({ form, field, ...props }) => {
  const { name, value } = field;
  const { handleChange, handleBlur } = form;
  const { label, className, disabled, inputProps } = props;

  return (
    <FormControlLabel
      control={
        <Checkbox
          id={name}
          name={name}
          checked={value}
          onChange={handleChange}
          onBlur={handleBlur}
          disabled={disabled}
          color="primary"
          {...inputProps}
        />
      }
      label={label}
      className={className}
    />
  );
};
