import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme } from "@mui/material/styles";
import { Link } from "react-router-dom";
import { Field, Formik, Form } from "formik";
import * as Yup from "yup";
import { InputTextField } from "../common/InputTextField";
import { InputCheckbox } from "../common/Checkbox";
const defaultTheme = createTheme();
const initialState = {
  usernameOrEmail: "",
  password: "",
  remember: false,
};

const FormValidationSchema = Yup.object({
  usernameOrEmail: Yup.string().required("Full name is required"),
  password: Yup.string().required("password is required"),
});

const SignIn = (props) => {
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <Box
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <Formik
          initialValues={initialState}
          validationSchema={FormValidationSchema}
          onSubmit={(values, { setFieldValue }) => {
            props?.handleSignIn(values);
          }}
        >
          {({ values, setFieldValue, touched, errors }) => {
            return (
              <Form>
                <Grid container spacing={2} mt={2}>
                  <Grid item xs={12}>
                    <Field
                      name={`usernameOrEmail`}
                      value={`${values.usernameOrEmail}`}
                      label="Email/Username"
                      placeholder="Email/Username"
                      required
                      component={InputTextField}
                      autoComplete="email"
                      fullWidth

                      //error={
                      //touchedItems?.apartmentUnitHouseNumber &&
                      //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                      //}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Field
                      name={`password`}
                      value={`${values.password}`}
                      label="Password"
                      placeholder="Password"
                      type="password"
                      required
                      component={InputTextField}
                      autoComplete="password"
                      fullWidth

                      //error={
                      //touchedItems?.apartmentUnitHouseNumber &&
                      //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                      //}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Field
                      name={`remember`}
                      value={`${values.remember}`}
                      label="Remember me"
                      placeholder="Remember me"
                      component={InputCheckbox}
                      //error={
                      //touchedItems?.apartmentUnitHouseNumber &&
                      //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                      //}
                    />
                  </Grid>

                  <Grid item xs={12} md={12}>
                    <Button
                      type="submit"
                      fullWidth
                      variant="contained"
                      sx={{ mb: 2 }}
                    >
                      Sign In
                    </Button>
                  </Grid>
                  <Grid container>
                    <Grid item xs>
                      <Link to="/forgot" variant="body2">
                        Forgot password?
                      </Link>
                    </Grid>
                    <Grid item>
                      <Link to="/signup" variant="body2">
                        {"Don't have an account? Sign Up"}
                      </Link>
                    </Grid>
                  </Grid>
                </Grid>
              </Form>
            );
          }}
        </Formik>
      </Box>
    </Container>
  );
};

export default SignIn;
